.search-bar {
    width: 100%;
    padding: 0.5rem;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 9999px;
    border: none;
    outline: none;
    text-align: center;
    text-transform: uppercase;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    background-color: white;
  }
  
  .search-bar::placeholder {
    color: #000000;
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
    transition: opacity 0.3s ease;
  }
  
  /* optional if you want smoother hover globally without React state */
  .search-bar:hover {
    background-color: #ff0059;
  }
  