.page-container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

.contact-content {
  width: 100%;
  flex: 1;
  overflow: hidden;
}

/* Portrait layout with snap scroll */
.portrait-layout {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  scroll-snap-type: y mandatory;
  scroll-behavior: smooth;
  height: 100%;
}

/* Landscape layout */
.landscape-layout {
  display: flex;
  flex-direction: row;
  overflow-x: auto;
  height: 100%;
}

/* Common Section Styles */
.section {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2rem;
  color: white;
  scroll-snap-align: start;
  width: 100%;
}

/* Color Styles */
.section-1 {
  background-color: #e74c3c;
}

.section-2 {
  background-color: #bfdb34;
}

.section-3 {
  background-color: #2ecc71;
}

/* Landscape widths */
@media (orientation: landscape) {
  .horizontal-section.section-1 {
    width: 33.33%;
  }

  .horizontal-section.section-2 {
    width: 33.33%;
  }

  .horizontal-section.section-3 {
    width: 33.33%;
  }
}

/* Contact Info Styling */
.contact-info {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
}

.contact-info h2 {
  font-size: 2rem;
  margin: 0;
}

.contact-info p {
  font-size: 1.2rem;
  margin: 0;
  color: #f0f0f0;
}

.contact-links {
  display: flex;
  gap: 20px;
  margin-top: 10px;
}

.contact-links a {
  color: white;
  text-decoration: underline;
  font-size: 1.1rem;
  transition: color 0.2s ease;
}

.contact-links a:hover {
  color: #ddd;
}

/* Volklec link in job title */
.company-link {
  color: white;
  text-decoration: underline;
  font-weight: 500;
  transition: color 0.2s ease;
}

.company-link:hover {
  color: #ddd;
}

